<template>
    <p-dialog
        header="Edit"
        v-model:visible="editDialog"
        :style="{ width: '400px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle Type
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.cycleType"
                    id="cycle-type"
                    :options="cycleTypeOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Funded
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.ohip"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Cycle Cancelled
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.cancelled"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Sperm Source
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.spermSource"
                    id="cycle-type"
                    :filter="true"
                    :options="spermSourceOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Egg Source
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="cycle.eggSource"
                    id="cycle-type"
                    :filter="true"
                    :options="eggSourceOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
        </div>

        <div class="p-field p-grid">
            <label for="firstname" class="p-col-4 p-mb-0">
                Egg Donor
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.eggDonor"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <div class="p-field p-grid p-mb-4">
            <label for="firstname" class="p-col-4 p-mb-0">
                Complete
            </label>
            <div class="p-col">
                <p-toggle-button
                    v-model="cycle.complete"
                    id="egg-donor"
                    onIcon="pi pi-check"
                    offIcon="pi pi-times"
                />
            </div>
        </div>

        <p-button label="Save" class="p-mr-2" @click="saveCycle" />
        <p-button
            label="Cancel"
            class="p-button-danger p-button-outlined"
            @click="editDialog = false"
        />
    </p-dialog>
</template>

<script>
import { inject, onMounted, watch } from "vue";
import { emitter } from "@/event-bus";
import { useArtCycleForm } from "@/composable/art-cycle";

export default {
    setup() {
        const {
            store,
            selectedCycle,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle,
            fillupForm,
            loading
        } = useArtCycleForm();

        watch(selectedCycle, fillupForm);
        const editDialog = inject("editDialog");

        const saveCycle = () => {
            loading.value = true;
            store
                .dispatch("artCycle/updateCycle", {
                    cycleNo: selectedCycle.value.cycleNo,
                    cycleId: selectedCycle.value.cycleId,
                    cycleType: cycle.cycleType,
                    cycleNoType: cycle.cycleNoType,
                    spermSource: cycle.spermSource,
                    eggSource: cycle.eggSource,
                    eggDonor: !!cycle.eggDonor,
                    cycleIncomplete: !cycle.complete,
                    cycleComplete: !!cycle.complete,
                    ohip: cycle.ohip,
                    cycleCancelled: !!cycle.cancelled,
                    others: cycle.others,
                    cycleDate: selectedCycle.value.cycleDate
                })
                .then(() => {
                    editDialog.value = false;
                    return store.dispatch(
                        "artCycle/getCyclesDetails",
                        store.state.artCycle.patient.id
                    );
                })
                .catch(() => {})
                .finally(() => {
                    loading.value = false;
                });
        };

        onMounted(() => {
            if (selectedCycle.value) {
                fillupForm(selectedCycle.value);
            }
            store.dispatch("artCycle/getCycleFormFieldOptions");
            emitter.on("cycle-table:save-cycle", () => saveCycle());
        });

        onMounted(() => {
            emitter.off("cycle-table:save-cycle", saveCycle);
        });

        return {
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle,
            editDialog,
            saveCycle
        };
    }
};
</script>

<style scoped>
.p-dropdown {
    width: 100%;
}
</style>
