<template>
    <p-card>
        <template #content>
            <div class="p-fluid">
                <div class="p-field p-grid">
                    <label
                        for="firstname"
                        class="p-col-fixed p-text-bold p-mb-2 p-md-2 p-mb-md-0"
                        style="width: 100px; font-size: 16px"
                    >
                        Male
                    </label>
                    <div class="p-col">
                        <p-auto-complete
                            v-model="demographic"
                            :suggestions="fillteredDemographic"
                            :disabled="disabled"
                            @complete="searchDemographic($event)"
                            @item-select="onDemographicSelect($event)"
                            field="label"
                            placeholder="Type to search"
                        />
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, onMounted, ref, watch } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import Service from "@/service/art-cycle.js";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();

        const title = ref("Male");
        const demographic = ref();
        const fillteredDemographic = ref();
        const partner = computed(() => store.state.artCycle.partner);
        const disabled = ref(false);

        const searchDemographic = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "M".toLowerCase(),
            })
                .then((resp) => {
                    fillteredDemographic.value = resp.data;
                })
                .catch((err) => {
                    emitter.emit(EVENT_STRINGS.LOGOUT);
                });
        };

        const onDemographicSelect = ({ value }) => {
            emitter.emit("partner-search:partner-selected", value);
        };

        watch(partner, (partner) => {
            demographic.value = {
                ...partner,
                label: `${partner.id} - ${partner.firstName} ${partner.lastName}`,
            };
            disabled.value = true;
        });

        onMounted(() => {
            let partner = store.state.artCycle.partner;
            if (partner) {
                demographic.value = {
                    ...partner,
                    label: `${partner.id} - ${partner.firstName} ${partner.lastName}`,
                };
                disabled.value = true;
            }
        });

        return {
            title,
            demographic,
            disabled,
            fillteredDemographic,
            searchDemographic,
            onDemographicSelect,
        };
    },
};
</script>