import { computed, inject, reactive, ref } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import Service from "@/service/art-cycle.js";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";

export function usePatientSearch() {
    const store = useStore();

    const title = ref("Female");
    const demographic = ref();
    const fillteredDemographic = ref();
    const loading = inject("loading");

    const searchDemographic = (event) => {
        Service.searchDemographic({
            term: event.query.trim(),
            sex: "F".toLowerCase()
        })
            .then((resp) => {
                fillteredDemographic.value = resp.data;
            })
            .catch((err) => {
                emitter.emit(EVENT_STRINGS.LOGOUT);
            });
    };

    const onDemographicSelect = ({ value: { demographicNo } }) => {
        loading.value = true;
        store
            .dispatch("artCycle/getCyclesDetails", demographicNo)
            .then(() => (loading.value = false));
    };

    return {
        store,
        title,
        demographic,
        fillteredDemographic,
        loading,
        searchDemographic,
        onDemographicSelect
    };
}

export function useArtCycleForm() {
    const store = useStore();
    const selectedCycle = computed(() => store.state.artCycle.selectedCycle);
    const cycleTypeOptions = computed(
        () => store.state.artCycle.cycleTypeOptions
    );
    const spermSourceOptions = computed(
        () => store.state.artCycle.spermSourceOptions
    );
    const eggSourceOptions = computed(
        () => store.state.artCycle.eggSourceOptions
    );

    const cycle = reactive({
        cycleId: null,
        cycleType: null,
        funded: null,
        cancelled: null,
        spermSource: null,
        eggSource: null,
        eggDonor: null,
        complete: null
    });

    const fillupForm = (data) => {
        cycle.cycleId = data.cycleId;
        cycle.cycleType = data.cycleType;
        cycle.cancelled = data.cancel;
        cycle.spermSource = data.spermSource;
        cycle.eggSource = data.eggSource;
        cycle.complete = data.cycleCompleted;
        cycle.ohip = data.ohip;
    };

    const loading = inject("loading");

    return {
        store,
        selectedCycle,
        cycleTypeOptions,
        spermSourceOptions,
        eggSourceOptions,
        cycle,
        fillupForm,
        loading
    };
}

export function useMergeCycleDialogInit() {
    const store = useStore();
    const toast = useToast();
    const mergeCycleDailog = inject("mergeCycleDailog");
    const cycleSummary = computed(() => store.state.artCycle.cyclesSummary);
    const cycle1Selected = ref(null);
    const cycle2Selected = ref(null);
    const result = ref();

    const mergeCycle = (dryRun = false) => {
        if (cycle1Selected.value == null && cycle2Selected.value == null) {
            toast.add({
                severity: "error",
                summary: "Error",
                detail: "Please select 2 cycles first",
                life: 3000
            });
            return;
        }

        store
            .dispatch("artCycle/mergeCycle", {
                cycle1: cycle1Selected.value.cycleId,
                cycle2: cycle2Selected.value.cycleId,
                dryRun: dryRun
            })
            .then((data) => {
                result.value = data;
                emitter.emit("merge-cycle-dialog:merge-cycle");
            });
    };

    return {
        mergeCycleDailog,
        cycleSummary,
        cycle1Selected,
        cycle2Selected,
        mergeCycle,
        result
    };
}
