<template>
    <p-card>
        <template #content>
            <div class="p-fluid">
                <div class="p-field p-grid">
                    <label
                        for="firstname"
                        class="p-col-fixed p-text-bold p-mb-2 p-md-2 p-mb-md-0"
                        style="width: 100px; font-size: 16px"
                    >
                        Female
                    </label>
                    <div class="p-col">
                        <p-auto-complete
                            v-model="demographic"
                            :suggestions="fillteredDemographic"
                            @complete="searchDemographic($event)"
                            @item-select="onDemographicSelect($event)"
                            field="label"
                            placeholder="Type to search"
                        />
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { onMounted } from "vue";
import { emitter } from "@/event-bus";

import { usePatientSearch } from "@/composable/art-cycle";

export default {
    setup() {
        const {
            store,
            title,
            demographic,
            fillteredDemographic,
            loading,
            searchDemographic,
            onDemographicSelect
        } = usePatientSearch();

        onMounted(() => {
            let patient = store.state.artCycle.patient;
            if (patient) {
                demographic.value = {
                    ...patient,
                    label: `${patient.id} - ${patient.firstName} ${patient.lastName}`
                };
            }
        });

        return {
            title,
            demographic,
            fillteredDemographic,
            searchDemographic,
            onDemographicSelect
        };
    }
};
</script>
